<template>
 <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency }}
          </td>
          <td class="text-right">
            {{ item.price | currency }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm mr-1" @click="openModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="deleteModalShow(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
<Pagination :allPages='pagination' @update='getProducts'></Pagination>
<!-- Modal -->
<div class="modal fade" id="productModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-lg" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-dark text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span></span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-sm-4">
            <div class="form-group">
              <label for="image"></label>
              <input type="text" class="form-control" id="image"
                v-model="tempProduct.imageUrl"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="customFile"> 
                <i class="fas fa-spinner fa-spin"
                v-if="status.fileUploading"></i>
              </label>
              <input type="file" id="customFile" class="form-control"
                @change="uploadFile"
                ref="files">
            </div>
            <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
              class="img-fluid"
              :src="tempProduct.imageUrl"
              alt="">
          </div>
          <div class="col-sm-8">
            <div class="form-group">
              <label for="title"></label>
              <input type="text"
              class="form-control"
              id="title"
              v-model="tempProduct.title"
              placeholder="">
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="category"></label>
                <input type="text" class="form-control" id="category"
                  v-model="tempProduct.category"
                  placeholder="">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="unit" class="form-control" id="unit"
                  v-model="tempProduct.unit"
                  placeholder="">
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
              <label for="origin_price"></label>
                <input type="number" class="form-control" id="origin_price"
                  v-model="tempProduct.origin_price"
                  placeholder="">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="number" class="form-control" id="price"
                  v-model="tempProduct.price"
                  placeholder="">
              </div>
            </div>
            <hr>

            <div class="form-group">
              <label for="description"></label>
              <textarea type="text" class="form-control" id="description"
                v-model="tempProduct.description"
                placeholder=""></textarea>
            </div>
            <div class="form-group">
              <label for="content"></label>
              <textarea type="text" class="form-control" id="content"
                v-model="tempProduct.content"
                placeholder=""></textarea>
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  v-model="tempProduct.is_enabled"
                  :true-value="1"
                  :false-value="0"
                  id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
      </div>
    </div>
  </div>
</div>
<div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-danger text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span></span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
         {{tempProduct.title}}<strong class="text-danger"></strong> ()
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
        <button type="button" class="btn btn-danger"
        @click="deleteProduct"
          ></button>
      </div>
    </div>
  </div>
</div>
  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from './Pagination'

export default {
  components: {
    Pagination
  },
  data () {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false
      }
    }
  },
  methods: {
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/products?page=${page}`
      const vm = this
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    openModal (isNew, item) {
      if (isNew) {
        this.tempProduct = {}
        this.isNew = true
      } else {
        this.tempProduct = Object.assign({}, item)
        this.isNew = false
      }
      $('#productModal').modal('show')
    },
    updateProduct () {
      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product`
      let httpMethod = 'post'
      const vm = this
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          $('#productModal').modal('hide')
          vm.getProducts()
        } else {
          $('#productModal').modal('hide')
          vm.getProducts()
          console.log('')
        }
      })
    },
    deleteModalShow (item) {
      this.tempProduct = Object.assign({}, item)
      $('#delProductModal').modal('show')
    },
    deleteProduct () {
      const vm = this
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product/${vm.tempProduct.id}`
      this.$http.delete(api).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          $('#delProductModal').modal('hide')
          vm.getProducts()
        } else {
          $('#delProductModal').modal('hide')
          vm.getProducts()
          console.log('')
        }
      })
    },
    uploadFile () {
      const uploadFile = this.$refs.files.files[0]
      const vm = this
      const formData = new FormData()
      formData.append('file-to-upload', uploadFile)
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/upload`
      vm.status.fileUploading = true
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        console.log(response.data)
        vm.status.fileUploading = false
        if (response.data.success) {
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl) // 
        } else {
          this.$bus.$emit('message:push', response.data.message, 'danger')
        }
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
